import React, { Component } from "react";
import moment from "moment";
import EditableTable from "./editableTable";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  Select,
  Col,
  InputNumber,
  DatePicker,
  Popconfirm,
  Steps,
  Row,
  Divider,
  Radio,
  Empty
} from "antd";
import { customDot } from "../../../App/app.style";
import notification from "../../../../components/notification";
import ModalCancel from "./modalCancel";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const InputGroup = Input.Group;
const Step = Steps.Step;

class FixedAssetUpdateForm extends Component {
  state = {
    check: 2,
    temp_fixed_asset_expenditure: null,
    temp_fixed_asset_expenditures: [],
    fixed_asset_expenditures: [],
    total_asset_value: 0,
    book_value: 0,
    visibleModalFixedAsset: false,
    search: null,
    clear: false,
    check_code: false,
    visibleModalCancel: false
  };

  componentDidUpdate = () => {
    if (this.state.check === 2 && this.props.fixedAsset.fixed_asset !== null) {
      const { fixed_asset } = this.props.fixedAsset;
      const { setFieldsValue } = this.props.form;

      setFieldsValue({
        id_asset_category: fixed_asset.id_asset_category,
        id_asset_type: fixed_asset.id_asset_type,
        id_location: fixed_asset.id_location,
        id_department: fixed_asset.id_department,
        fixed_asset_code: fixed_asset.fixed_asset_code,
        fixed_asset_name: fixed_asset.fixed_asset_name,
        serial_no: fixed_asset.serial_no,
        acquisition_date: moment(
          fixed_asset.acquisition_date,
          "YYYY-MM-DD HH:mm:ss"
        ),
        usage_date: moment(fixed_asset.usage_date, "YYYY-MM-DD"),
        depreciation_end_date: moment(
          fixed_asset.depreciation_end_date,
          "YYYY-MM-DD"
        ),
        depreciation_method: fixed_asset.depreciation_method,
        estimated_life_year: fixed_asset.estimated_life_year,
        estimated_life_month: fixed_asset.estimated_life_month,
        depreciation_rate: fixed_asset.depreciation_rate,
        intangible: fixed_asset.intangible,
        qty: fixed_asset.qty,
        salvage_value: fixed_asset.salvage_value,
        depreciation_value:
          fixed_asset.status === 1 ? 0 : fixed_asset.depreciation_value,
        value_per_quantity:
          parseFloat(fixed_asset.qty) === 0
            ? 0
            : fixed_asset.status === 1
            ? parseFloat(fixed_asset.asset_value) / parseFloat(fixed_asset.qty)
            : parseFloat(fixed_asset.book_value) / parseFloat(fixed_asset.qty),
        monthly_depreciation: fixed_asset.monthly_depreciation,
        id_account_asset: fixed_asset.id_account_asset,
        id_account_depreciation: fixed_asset.id_account_depreciation,
        id_account_expense: fixed_asset.id_account_expense
      });

      let fixed_asset_expenditures = [];
      let total_asset_value = 0;

      fixed_asset.fixed_asset_expenditures.map((data, key) => {
        let fixed_asset_expenditure = {
          index_no: data.index_no,
          id_account: data.id_account,
          account_code: data.account_code,
          account_name: data.account_name,
          amount: parseFloat(data.amount),
          id_sub_ledger: data.id_sub_ledger
        };
        fixed_asset_expenditures.push(fixed_asset_expenditure);
        total_asset_value += parseFloat(data.amount);
      });

      if (fixed_asset.status > 1) {
        total_asset_value = fixed_asset.asset_value;
      }
      // console.log(total_asset_value);

      this.setState({
        temp_fixed_asset_expenditures: fixed_asset_expenditures,
        fixed_asset_expenditures: fixed_asset_expenditures,
        total_asset_value,
        book_value:
          fixed_asset.status === 1 ? total_asset_value : fixed_asset.book_value,
        check: 1
      });
    }
  };

  // Rumuse Depreciation Rate Boy
  depreciationRate = (year, month) => {
    if (year === 0) {
      return 0;
    }
    
    let rate_year = 100 / year;
    let rate_month =
      parseInt(month) !== 0 ? ((month / (year * 12 + month)) * 100) / year : 0;
    let rate = rate_year - rate_month;

    return rate;
  };

  // Rumus Monthly Depreciation
  monthlyDepreciation = (total_asset_min_salvage, rate) => {
    let monthly_depreciation = total_asset_min_salvage
      ? ((total_asset_min_salvage / 12) * rate) / 100
      : 0;
    return monthly_depreciation !== 0 ? monthly_depreciation : 0;
  };

  // Rumus value per-quantity
  valuePerQuantity = (total_asset_value, quantity) => {
    let value_per_quantity =
      total_asset_value === 0 || quantity === 0
        ? 0
        : total_asset_value / quantity;

    return !isNaN(value_per_quantity) && value_per_quantity !== 0
      ? value_per_quantity
      : 0;
  };

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    });
  };

  handleSave = (fixed_asset_expenditures, total_asset_value) => {
    const { getFieldValue, setFieldsValue } = this.props.form;

    this.setState({
      fixed_asset_expenditures: fixed_asset_expenditures,
      total_asset_value
    });

    //let estimated_life        = getFieldValue('estimated_life_year') * 12 + getFieldValue('estimated_life_month')
    let depreciation_rate = getFieldValue("depreciation_rate");
    let quantity = getFieldValue("qty");
    let monthly_depreciation = this.monthlyDepreciation(
      total_asset_value -
        (getFieldValue("salvage_value") ? getFieldValue("salvage_value") : 0),
      depreciation_rate
    );
    let value_per_quantity = this.valuePerQuantity(total_asset_value, quantity);

    setFieldsValue({
      monthly_depreciation,
      value_per_quantity
    });
  };

  clearFixedAssetExpenditure = (
    fixed_asset_expenditures,
    total_asset_value
  ) => {
    const { getFieldValue, setFieldsValue } = this.props.form;

    this.setState({
      temp_fixed_asset_expenditure: null,
      fixed_asset_expenditures: fixed_asset_expenditures,
      total_asset_value,
      clear: false
    });

    //let estimated_life        = getFieldValue('estimated_life_year') * 12 + getFieldValue('estimated_life_month')
    let depreciation_rate = getFieldValue("depreciation_rate");
    let quantity = getFieldValue("qty");
    let monthly_depreciation = this.monthlyDepreciation(
      total_asset_value -
        (getFieldValue("salvage_value") ? getFieldValue("salvage_value") : 0),
      depreciation_rate
    );
    let value_per_quantity = this.valuePerQuantity(total_asset_value, quantity);

    setFieldsValue({
      monthly_depreciation,
      value_per_quantity
    });
  };

  clearFixedAssetExpenditures = (
    fixed_asset_expenditures,
    total_asset_value
  ) => {
    const { getFieldValue, setFieldsValue } = this.props.form;
    const { fixed_asset } = this.props.fixedAsset;

    let asset_value =
      fixed_asset.status === 1
        ? total_asset_value
        : this.state.total_asset_value;

    this.setState({
      temp_fixed_asset_expenditures: [],
      fixed_asset_expenditures: fixed_asset_expenditures,
      total_asset_value: asset_value,
      clear: false
    });

    //let estimated_life        = getFieldValue('estimated_life_year') * 12 + getFieldValue('estimated_life_month')
    let depreciation_rate = getFieldValue("depreciation_rate");
    let quantity = getFieldValue("qty");
    let monthly_depreciation = this.monthlyDepreciation(
      asset_value -
        (getFieldValue("salvage_value") ? getFieldValue("salvage_value") : 0),
      depreciation_rate
    );
    let value_per_quantity = this.valuePerQuantity(asset_value, quantity);

    setFieldsValue({
      monthly_depreciation,
      value_per_quantity
    });
  };

  handleChangeExpenditureAccount = e => {
    const { sub_ledger_accounts } = this.props.fixedAsset;
    const [account] = sub_ledger_accounts.filter(
      data => data.account_code + " " + data.account_name === e
    );
    this.setState({
      temp_fixed_asset_expenditure: account
    });
  };

  handleSelectLocation = id_location => {
    //const { getItems } = this.props;
    this.setState({
      id_location: id_location,
      clear: this.state.id_location !== id_location ? true : false
    });
  };

  handleChangeAssetCategory = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      id_asset_type: null
    });
  };

  handleChangeAssetType = value => {
    const { asset_types } = this.props.fixedAsset;
    const { getFieldValue, setFieldsValue } = this.props.form;

    const asset_type = asset_types.find(data => data.id_asset_type === value);

    setFieldsValue({
      depreciation_method: asset_type.depreciation_method,
      depreciation_rate: asset_type.depreciation_rate,
      estimated_life_year: asset_type.estimated_life_year,
      estimated_life_month: 0,
      id_account_asset: asset_type.id_account_asset,
      id_account_depreciation: asset_type.id_account_depreciation,
      id_account_expense: asset_type.id_account_expense
    });

    this.handleChangeAcquisitionDate(getFieldValue("acquisition_date"));

    //let estimated_life        = getFieldValue('estimated_life_year') * 12 + getFieldValue('estimated_life_month')
    let depreciation_rate = getFieldValue("depreciation_rate");
    let quantity = getFieldValue("qty");
    let monthly_depreciation = this.monthlyDepreciation(
      this.state.total_asset_value -
        (getFieldValue("salvage_value") ? getFieldValue("salvage_value") : 0),
      depreciation_rate
    );
    let value_per_quantity = this.valuePerQuantity(
      this.state.total_asset_value,
      quantity
    );

    setFieldsValue({
      monthly_depreciation,
      value_per_quantity
    });
  };

  handleChangeAcquisitionDate = (
    value,
    estimated_life_year,
    estimated_life_month
  ) => {
    const { getFieldValue, setFieldsValue } = this.props.form;
    let depreciation_end_date = moment(
      value.format("YYYY-MM-DD"),
      "YYYY-MM-DD"
    );
    let months =
      (estimated_life_year && !isNaN(estimated_life_year)
        ? estimated_life_year
        : getFieldValue("estimated_life_year")
        ? getFieldValue("estimated_life_year")
        : 0) *
        12 +
      (estimated_life_month && !isNaN(estimated_life_month)
        ? estimated_life_month
        : getFieldValue("estimated_life_month")
        ? getFieldValue("estimated_life_month")
        : 0);

    depreciation_end_date.add(parseInt(months), "month");

    if (value > getFieldValue("usage_date")) {
      setFieldsValue({
        usage_date: value
      });
    }

    setFieldsValue({
      depreciation_end_date: depreciation_end_date
    });
  };

  handleChangeFromDepreciationMethod = e => {
    const { getFieldValue, setFieldsValue } = this.props.form;

    let value = e.target.value;
    let depreciation_rate = this.depreciationRate(
      getFieldValue("estimated_life_year"),
      getFieldValue("estimated_life_month")
    );

    if (value === 1) {
      setFieldsValue({
        depreciation_rate: 0
      });
    } else if (value === 2) {
      setFieldsValue({
        depreciation_rate: depreciation_rate
      });
    } else if (value === 3) {
      setFieldsValue({
        depreciation_rate: depreciation_rate * 2
      });
    }

    let monthly_depreciation = this.monthlyDepreciation(
      this.state.total_asset_value -
        (getFieldValue("salvage_value") ? getFieldValue("salvage_value") : 0),
      getFieldValue("depreciation_rate")
    );
    setFieldsValue({
      monthly_depreciation
    });
  };

  handleChangeFromEstimatedLifeYear = value => {
    const { getFieldValue, setFieldsValue } = this.props.form;
    let depreciation_method = getFieldValue("depreciation_method");
    let depreciation_rate = this.depreciationRate(
      value,
      getFieldValue("estimated_life_month")
    );

    if (depreciation_method === 1) {
      setFieldsValue({
        depreciation_rate: 0
      });
    } else if (depreciation_method === 2) {
      setFieldsValue({
        depreciation_rate: depreciation_rate
      });
    } else if (depreciation_method === 3) {
      setFieldsValue({
        depreciation_rate: depreciation_rate * 2
      });
    }

    this.handleChangeAcquisitionDate(getFieldValue("acquisition_date"), value);

    let monthly_depreciation = this.monthlyDepreciation(
      this.state.total_asset_value -
        (getFieldValue("salvage_value") ? getFieldValue("salvage_value") : 0),
      getFieldValue("depreciation_rate")
    );
    setFieldsValue({
      monthly_depreciation
    });
  };

  handleChangeFromEstimatedLifeMonth = value => {
    const { getFieldValue, setFieldsValue } = this.props.form;
    let depreciation_method = getFieldValue("depreciation_method");
    let depreciation_rate = this.depreciationRate(
      getFieldValue("estimated_life_year"),
      value
    );

    if (depreciation_method === 1) {
      setFieldsValue({
        depreciation_rate: 0
      });
    } else if (depreciation_method === 2) {
      setFieldsValue({
        depreciation_rate: depreciation_rate
      });
    } else if (depreciation_method === 3) {
      setFieldsValue({
        depreciation_rate: depreciation_rate * 2
      });
    }

    this.handleChangeAcquisitionDate(
      getFieldValue("acquisition_date"),
      null,
      value
    );

    let monthly_depreciation = this.monthlyDepreciation(
      this.state.total_asset_value -
        (getFieldValue("salvage_value") ? getFieldValue("salvage_value") : 0),
      getFieldValue("depreciation_rate")
    );
    setFieldsValue({
      monthly_depreciation
    });
  };

  handleChangeQuantity = value => {
    const { setFieldsValue } = this.props.form;

    let value_per_quantity = this.valuePerQuantity(
      this.state.total_asset_value,
      value
    );

    setFieldsValue({
      value_per_quantity
    });
  };

  handleChangeSalvageValue = value => {
    const { getFieldValue, setFieldsValue } = this.props.form;

    let monthly_depreciation = this.monthlyDepreciation(
      this.state.total_asset_value - value,
      getFieldValue("depreciation_rate")
    );
    setFieldsValue({
      monthly_depreciation
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { messages } = this.props.fixedAsset;
      // console.log(err)
      if (this.state.fixed_asset_expenditures.length < 1) {
        notification("error", messages.expenditure_required);
      } else if (this.state.total_asset_value === 0) {
        notification("error", messages.zero_amount);
      } else if (!err) {
        values.acquisition_date = values.acquisition_date.format(
          "YYYY-MM-DD HH:mm:ss"
        );
        values.usage_date = values.usage_date.format("YYYY-MM-DD");
        values.depreciation_end_date = values.depreciation_end_date.format(
          "YYYY-MM-DD"
        );
        values.fixed_asset_expenditures = [];
        this.state.fixed_asset_expenditures.map(data => {
          if (parseFloat(data.amount) > 0) {
            values.fixed_asset_expenditures.push(data);
          }
        });
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { fixedAsset, processFixedAsset, cancelFixedAsset } = this.props;
    const { loading, asset_categories, base_currency } = fixedAsset;

    const data = fixedAsset.fixed_asset ? fixedAsset.fixed_asset : [];

    let permissions = this.props.permissions;

    const error = fixedAsset.errorField ? fixedAsset.errorField : [];

    let acquisition_date = getFieldValue("acquisition_date")
      ? moment(
          getFieldValue("acquisition_date").format("YYYY-MM-DD HH:mm:ss")
        ).subtract(1, "seconds")
      : moment().subtract(1, "seconds");

    //console.log(this.state);

    const loop = array =>
      array.map(data => {
        let perData = {
          title:
            data.asset_category_code + " (" + data.asset_category_name + ")",
          value: data.id_asset_category,
          key: data.id_asset_category,
          children: data.children ? loop(data.children) : null
        };

        return perData;
      });

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loading}
        />
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
           <IntlMessages id="fixedAssets.add.subtitle.data" />
         </Divider> */}
        <Row style={{ margin: "24px 0px" }}>
          <Col span={16} justify="center">
            <Steps
              size="small"
              progressDot={customDot}
              current={data.status - 1}
            >
              <Step title={<IntlMessages id="new" />} />
              <Step title={<IntlMessages id="processed" />} />
              <Step title={<IntlMessages id="cancelled" />} />
            </Steps>
          </Col>
        </Row>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 15px 0 0" }}
          >
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetCategories.pageHeader" />}
                >
                  {getFieldDecorator("id_asset_category", {
                    //initialValue: data.id_asset_category,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      disabled={data.status === 1 ? false : true}
                    >
                      {asset_categories.map((data, key) => (
                        <Option key={key} value={data.id_asset_category}>
                          {data.asset_category_code +
                            " (" +
                            data.asset_category_name +
                            ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetTypes.pageHeader" />}
                >
                  {getFieldDecorator("id_asset_type", {
                    // initialValue: data.id_asset_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAsset.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      onChange={this.handleChangeAssetType}
                      disabled={data.status === 1 ? false : true}
                      notFoundContent={
                        getFieldValue("id_asset_category") ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noAssetCategory" />}
                          />
                        )
                      }
                    >
                      {fixedAsset.asset_types.map((asset_type, key) =>
                        asset_type.id_asset_category ===
                        getFieldValue("id_asset_category") ? (
                          <Option key={key} value={asset_type.id_asset_type}>
                            {asset_type.asset_type_code +
                              " (" +
                              asset_type.asset_type_name +
                              ")"}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.pageHeader" />}
                >
                  {getFieldDecorator("id_location", {
                    // initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAsset.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleSelectLocation}
                      style={{ width: "100%" }}
                      disabled={data.status === 1 ? false : true}
                    >
                      {fixedAsset.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="departments.pageHeader" />}
                >
                  {getFieldDecorator("id_department", {
                    // initialValue: data.id_gl_account,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAsset.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      disabled={data.status === 1 ? false : true}
                    >
                      {fixedAsset.departments.map((data, key) => (
                        <Option key={key} value={data.id_department}>
                          {data.department_code +
                            " (" +
                            data.department_name +
                            ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="fixedAssets.add.code" />}
              validateStatus={
                error.fixed_asset_code || getFieldError("fixed_asset_code")
                  ? "error"
                  : null
              }
              help={
                error.fixed_asset_code
                  ? error.fixed_asset_code
                  : getFieldError("fixed_asset_code")
              }
            >
              {getFieldDecorator("fixed_asset_code", {
                //initialValue: data.asset_type_code,
                rules: [
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={data.status === 1 ? false : true}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="fixedAssets.add.name" />}
            >
              {getFieldDecorator("fixed_asset_name", {
                //initialValue: data.fixed_asset_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={data.status === 1 ? false : true} />)}
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssets.add.serialNo" />}
                >
                  {getFieldDecorator("serial_no", {
                    //initialValue: data.serial_no,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 50,
                        message: <IntlMessages id="maxLength50" />
                      }
                    ]
                  })(<Input disabled={data.status === 1 ? false : true} />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="acquisitionDate" />}
                >
                  {getFieldDecorator("acquisition_date", {
                    initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime={true}
                      onChange={this.handleChangeAcquisitionDate}
                      disabled={data.status === 1 ? false : true}
                      // onOpenChange={this.handleChangeTransDate}
                      // disabled={
                      //   !permissions.includes("Can Back Date")
                      //     ? true
                      //     : false
                      // }
                      allowClear={false}
                      //disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="usageDate" />}
                >
                  {getFieldDecorator("usage_date", {
                    initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY"
                      // onChange={this.handleChangeTransDate}
                      // onOpenChange={this.handleChangeTransDate}
                      allowClear={false}
                      disabledDate={e => e.valueOf() < acquisition_date}
                      disabled={data.status === 1 ? false : true}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="depreciationEndDate" />}
                >
                  {getFieldDecorator("depreciation_end_date", {
                    initialValue: moment().add(12, "month"),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY"
                      allowClear={false}
                      disabled={true}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </div>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 0 0 15px" }}
          >
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="assetFiscals.add.depreciationMethod" />}
            >
              {getFieldDecorator("depreciation_method", {
                initialValue: 2,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group
                  style={{ display: "flex" }}
                  disabled={data.status === 1 ? false : true}
                  buttonStyle="solid"
                  onChange={this.handleChangeFromDepreciationMethod}
                  loading={loading}
                >
                  <Radio.Button
                    style={{ flex: "1", textAlign: "center" }}
                    value={1}
                  >
                    <IntlMessages id="Non Depreciable" />
                  </Radio.Button>
                  <Radio.Button
                    style={{ flex: "1", textAlign: "center" }}
                    value={2}
                  >
                    <IntlMessages id="Straight Line" />
                  </Radio.Button>
                  <Radio.Button
                    style={{ flex: "1", textAlign: "center" }}
                    value={3}
                  >
                    <IntlMessages id="Double Declining Method" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>

            <InputGroup>
              <Col span={9}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="assetFiscals.add.estimatedLifeYear" />
                  }
                >
                  {getFieldDecorator("estimated_life_year", {
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      min={getFieldValue("depreciation_method") === 3 ? 2 : 1}
                      precision={0}
                      onChange={this.handleChangeFromEstimatedLifeYear}
                      loading={loading}
                      style={{ width: "100%" }}
                      disabled={data.status === 1 ? false : true}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={9}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="assetFiscals.add.estimatedLifeMonth" />
                  }
                >
                  {getFieldDecorator("estimated_life_month", {
                    initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      min={
                        getFieldValue("depreciation_method") === 3 &&
                        getFieldValue("estimated_life_year") === 2
                          ? 1
                          : 0
                      }
                      max={11}
                      precision={0}
                      onChange={this.handleChangeFromEstimatedLifeMonth}
                      loading={loading}
                      style={{ width: "100%" }}
                      disabled={data.status === 1 ? false : true}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={6}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="intangible" />}
                >
                  {getFieldDecorator("intangible", {
                    //initialValue: data.depreciation_method,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Radio.Group
                      style={{ display: "flex" }}
                      buttonStyle="solid"
                      loading={loading}
                      disabled={data.status === 1 ? false : true}
                    >
                      <Radio.Button
                        style={{ flex: "1", textAlign: "center" }}
                        value={1}
                      >
                        <IntlMessages id="yes" />
                      </Radio.Button>
                      <Radio.Button
                        style={{ flex: "1", textAlign: "center" }}
                        value={0}
                      >
                        <IntlMessages id="no" />
                      </Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="assetFiscals.add.depreciationRate" />
                  }
                >
                  {getFieldDecorator("depreciation_rate", {
                    initialValue: 100,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      loading={loading}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetTypes.add.assetAccount" />}
                >
                  {getFieldDecorator("id_account_asset", {
                    //initialValue: data.id_account_asset,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAsset.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      disabled={data.status === 1 ? false : true}
                    >
                      {this.props.fixedAsset.accounts.map((data, key) =>
                        data.id_account_type === "06" ? (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="assetTypes.add.depreciationAccount" />
                  }
                >
                  {getFieldDecorator("id_account_depreciation", {
                    // initialValue: data.id_account_depreciation,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAsset.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      disabled={data.status === 1 ? false : true}
                    >
                      {this.props.fixedAsset.accounts.map((data, key) =>
                        data.id_account_type === "06" ? (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetTypes.add.expenseAccount" />}
                >
                  {getFieldDecorator("id_account_expense", {
                    // initialValue: data.id_account_expense,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAsset.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      disabled={data.status === 1 ? false : true}
                    >
                      {this.props.fixedAsset.accounts.map((data, key) =>
                        data.id_account_type === "15" ||
                        data.id_account_type === "16" ? (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetValue" />}
                >
                  {/* {getFieldDecorator("asset_value", {
                    initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })( */}
                  <InputNumber
                    value={this.state.total_asset_value}
                    disabled={true}
                    precision={2}
                    loading={loading}
                    style={{ width: "100%" }}
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  />
                  {/* )} */}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="quantity" />}
                >
                  {getFieldDecorator("qty", {
                    initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      //min={0.01}
                      precision={2}
                      onChange={this.handleChangeQuantity}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      disabled={data.status === 1 ? false : true}
                    />
                  )}
                </Form.Item>
              </Col>
              {/* <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="salvageValue" />}
                >
                  {getFieldDecorator("salvage_value", {
                    initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      min={0}
                      precision={2}
                      onChange= {this.handleChangeSalvageValue}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      disabled={data.status === 1 ? false : true}
                    />
                  )}
                </Form.Item>
              </Col>      */}
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accumulationOfDepreciation" />}
                >
                  {getFieldDecorator("depreciation_value", {
                    initialValue: 0
                    // rules: [
                    //   {
                    //     required: true,
                    //     message: <IntlMessages id="required" />
                    //   }
                    // ]
                  })(
                    <InputNumber
                      disabled={true}
                      precision={2}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="monthlyDepreciation" />}
                >
                  {getFieldDecorator("monthly_depreciation", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      precision={2}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="valuePerQuantity" />}
                >
                  {getFieldDecorator("value_per_quantity", {
                    //initialValue: data.qty,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      precision={2}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="bookValue" />}
                >
                  {/* {getFieldDecorator("book_value", {
                    initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })( */}
                  <InputNumber
                    value={
                      data.status === 1
                        ? this.state.total_asset_value
                        : this.state.book_value
                    }
                    disabled={true}
                    precision={2}
                    loading={loading}
                    style={{ width: "100%" }}
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  />
                  {/* )} */}
                </Form.Item>
              </Col>
            </InputGroup>
          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14, marginBottom: 0 }}>
          <IntlMessages id="fixedAssets.subtitle.expenditures" />
        </Divider>

        {data.status === 1 ? (
          <Form.Item
            {...formItemLayout}
            //label={<IntlMessages id="GLAccount" />}
          >
            {getFieldDecorator("id_account_fixed_asset_expenditure", {
              // initialValue: data.id_gl_account,
              // rules: [
              //   {
              //     required: true,
              //     message: <IntlMessages id="required" />
              //   }
              // ]
            })(
              <Select
                loading={this.props.fixedAsset.loading}
                showSearch
                optionFilterProp="children"
                placeholder={<IntlMessages id="select.expenditure" />}
                onChange={this.handleChangeExpenditureAccount}
              >
                {fixedAsset.sub_ledger_accounts.map((data, key) =>
                  data.id_account_type === "02" &&
                  data.id_location === getFieldValue("id_location") &&
                  data.id_currency === base_currency.id_currency ? (
                    <Option
                      key={key}
                      value={data.account_code + " " + data.account_name}
                      disabled={
                        data.sub_ledger_type === 1 || data.sub_ledger_type === 2
                          ? true
                          : false
                      }
                    >
                      {data.account_code + " " + data.account_name}
                    </Option>
                  ) : data.id_account_type === "02" ||
                    data.id_currency !== base_currency.id_currency ? null : (
                    <Option
                      key={key}
                      value={data.account_code + " " + data.account_name}
                      disabled={
                        data.sub_ledger_type === 1 || data.sub_ledger_type === 2
                          ? true
                          : false
                      }
                    >
                      {data.account_code + " " + data.account_name}
                    </Option>
                  )
                )}
              </Select>
            )}
          </Form.Item>
        ) : null}

        <EditableTable
          base_currency={base_currency}
          sub_ledger_accounts={this.props.fixedAsset.sub_ledger_accounts}
          temp_fixed_asset_expenditure={this.state.temp_fixed_asset_expenditure}
          temp_fixed_asset_expenditures={
            this.state.temp_fixed_asset_expenditures
          }
          fixed_asset_expenditures={this.state.fixed_asset_expenditures}
          clearFixedAssetExpenditure={this.clearFixedAssetExpenditure}
          clearFixedAssetExpenditures={this.clearFixedAssetExpenditures}
          handleSave={this.handleSave}
          id_location={getFieldValue("id_location")}
          status={data.status}
          clear={this.state.clear}
        />

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{
              marginTop: "20px",
              marginLeft: "50px",
              marginRight: "5px"
            }}
            loading={loading}
            disabled={data.status === 1 ? false : true}
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Process */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
            onConfirm={() => processFixedAsset(data.id_fixed_asset)}
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status === 1 && permissions.includes("Confirm Fixed Asset")
                  ? false
                  : true
              }
              loading={loading}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {/* Cancel */}

          <Button
            style={{ marginRight: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 2 &&
              permissions.includes("Cancel Fixed Asset") &&
              data.fixed_asset_depreciations.length === 0 &&
              data.fixed_asset_revaluations.length === 0 &&
              data.fixed_asset_transfers.length === 0
                ? false
                : true
            }
            loading={loading}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedFixedAssetUpdateForm = Form.create({ name: "update_fixed_asset" })(
  FixedAssetUpdateForm
);

export default WrappedFixedAssetUpdateForm;
