import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import moment from "moment"

// import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Col, Spin, Tooltip } from "antd";

//import Popconfirms from "../../../../components/feedback/popconfirm";
import actions from "../../../../redux/asset/fixedAsset/actions";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { StatusTag } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
// import { stat } from "fs";


// const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

const redirectFAR = "/dashboard/fixed-asset-revaluations"

class IndexRevaluation extends Component {

  

  render() {
    // console.log(this.props.fixedAsset.loading);
    const { fixedAsset } = this.props;
    const fixed_asset_revaluations = fixedAsset.fixed_asset ? fixedAsset.fixed_asset.fixed_asset_revaluations : [];
    const { loading } = fixedAsset;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(data);

    let columns = [
      {
        title: getLanguage({id: "date"}),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "25%",
        render: text => <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      },
      {
        title: getLanguage({id: "transactionType"}),
        dataIndex: "transaction_type",
        rowKey: "transaction_type",
        width: "25%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "primary";
              display = getLanguage({ id: "valueAdd"});
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "disposal"});
              break;
            case 3:
              className = "invoiced";
              display = getLanguage({ id: "fixedAssetSale"});
              break;
            default:
              className = "primary";
              display = getLanguage({ id: "valueAdd"});
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({id: "qtyChange"}),
        dataIndex: "qty",
        rowKey: "qty",
        width: "20%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({id: "amount"})} (IDR)`,
        dataIndex: "amount",
        rowKey: "amount",
        width: "20%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: "",
        dataIndex: "id_fixed_asset_revaluation",
        rowKey: "id_fixed_asset_revaluation",
        width: "10%",
        align: "center",
        render: (text) => (
          <>
            <Tooltip
              placement="topLeft"
              title={<IntlMessages id="tooltip.detail" />}
            >
              <Link to={`${redirectFAR}/update/${text}`}>
                <Button 
                  size="small" 
                  className="invoiceEdtBtn"
                >
                  <i className="ion-android-open" />
                </Button>
              </Link>      
            </Tooltip>
          </>
        )
      }
    ];


    Object.keys(fixed_asset_revaluations).map((fixed_asset_revaluation, index) => {
      return dataSource.push({
        ...fixed_asset_revaluations[fixed_asset_revaluation],
        key: fixed_asset_revaluation
      });
    });


    return (
      <>
        {/* <div 
          style={{ marginRight:"20px", float:"right" }}
        >
          <Button
            type="primary"
            onClick={this.handleModalAdd.bind(this)}
          >
            <IntlMessages id="button.addRevaluation" />
          </Button>
        </div> */}
          

        {this.props.fixedAsset.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : ( 

        <CardWrapper>
          {/* <Col span={1} /> */}
          <Col span={24} style={{ marginTop: "20px",padding: "0 20px 0 20px" }}>
            <div className="isoInvoiceTable">
              <Scrollbars style={{ width: "100%" }}>
                <TableWrapper
                  // size="small"
                  dataSource={dataSource}
                  columns={columns}
                  loading={loading}
                  className="invoiceListTable"
                  bordered={true}
                  pagination={true}
                />
              </Scrollbars>
            </div>
          </Col>
        </CardWrapper>

      )}
    </>
    );
  }
}

const WrappedIndexRevaluation = Form.create({ name: "update_fixed_asset_revaluation" })(IndexRevaluation);

//export default WrappedIndexRevaluation;

const mapStateToProps = state => {
  return { 
    fixedAsset: state.fixedAsset,
    auth : state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedIndexRevaluation);
