import React, { Component } from "react";

import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Col, Row, Modal, DatePicker } from "antd";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;

class ModalDetail extends Component {
  componentDidUpdate(prevProps) {
    const { maintenance_data } = this.props;
    const { setFieldsValue } = this.props.form;

    if (prevProps.maintenance_data !== maintenance_data) {
      setFieldsValue({
        service_date: moment(maintenance_data.service_date, "YYYY-MM-DD"),
        id_asset_service_type: maintenance_data.id_asset_service_type,
        service_parameter: maintenance_data.service_parameter,
        service_note: maintenance_data.service_note
      });
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { maintenance_data } = this.props;
        values.id_fixed_asset_maintenance =
          maintenance_data.id_fixed_asset_maintenance;
        values.service_date = values.service_date.format("YYYY-MM-DD");
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const { fixedAsset, loading } = this.props;
    // const error = pettyCashType.errorField ? pettyCashType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="updateMaintenance" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="50%"
        okText={<IntlMessages id="button.submit" />}
        cancelText={<IntlMessages id="button.cancel" />}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row type="flex" justify="start" gutter={6}>
            <Col span={24}>
              <Form.Item {...formItemLayout} label={<IntlMessages id="date" />}>
                {getFieldDecorator("service_date", {
                  initialValue: moment(),
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <DatePicker
                    style={{ width: "100%" }}
                    showTime
                    format="DD MMMM YYYY"
                    // onChange={this.handleChangeTransDate}
                    // onOpenChange={this.handleChangeTransDate}
                    allowClear={false}
                    disabledDate={e =>
                      fixedAsset.fixed_asset
                        ? e.valueOf() <
                          moment(
                            fixedAsset.fixed_asset.acquisition_date,
                            "YYYY-MM-DD"
                          )
                        : e.valueOf() < moment()
                    }
                  />
                )}
              </Form.Item>
            </Col>
          </Row>

          <Row>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetServiceTypes.pageHeader" />}
              >
                {getFieldDecorator("id_asset_service_type", {
                  // initialValue: data.id_asset_service_type,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={fixedAsset.loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {fixedAsset.fixed_asset !== null
                      ? fixedAsset.asset_service_types.map((data, key) =>
                          fixedAsset.fixed_asset.id_asset_category ===
                          data.id_asset_category ? (
                            <Option
                              key={key}
                              value={data.id_asset_service_type}
                            >
                              {data.description +
                                " " +
                                parseFloat(data.interval_value) +
                                " " +
                                getLanguage({ id: data.interval_unit })}
                            </Option>
                          ) : null
                        )
                      : null}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>

          <Row>
            <Col span={24}>
              <Form.Item
                {...formTextArea}
                label={<IntlMessages id="serviceParameter" />}
              >
                {getFieldDecorator("service_parameter", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>
            </Col>
          </Row>

          <Row>
            <Col span={24}>
              <Form.Item {...formTextArea} label={<IntlMessages id="note" />}>
                {getFieldDecorator("service_note", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(<TextArea rows={5} />)}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedDetailModal = Form.create({ name: "modal_update_maintenance" })(
  ModalDetail
);

export default WrappedDetailModal;
