import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment"

// import LayoutWrapper from "../../../../components/utility/layoutWrapper";
// import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Col, Spin, } from "antd";
//import Popconfirms from "../../../../components/feedback/popconfirm";
import actions from "../../../../redux/asset/fixedAsset/actions";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { StatusTag } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";


// const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class IndexDepreciation extends Component {


  render() {
    // console.log(this.props.fixedAsset.loading);
    const { fixedAsset } = this.props;
    const fixed_asset_depreciations = fixedAsset.fixed_asset ? fixedAsset.fixed_asset.fixed_asset_depreciations : [];
    const { loading } = fixedAsset;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(data);

    let columns = [
      {
        title: getLanguage({ id: "date"}),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "20%",
        render: text => <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      },
      {
        title: getLanguage({ id: "depreciationType"}),
        dataIndex: "depreciation_type",
        rowKey: "depreciation_type",
        width: "40%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "primary";
              display = getLanguage({ id: "disposalFA"})
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "periodic"})
              break;
            default:
              className = "primary";
              display = getLanguage({ id: "disposalFA"})
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: `${getLanguage({ id: "amount"})} (IDR)`,
        dataIndex: "amount",
        rowKey: "amount",
        width: "40%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      
    ];


    Object.keys(fixed_asset_depreciations).map((fixed_asset_depreciation, index) => {
      return dataSource.push({
        ...fixed_asset_depreciations[fixed_asset_depreciation],
        key: fixed_asset_depreciation
      });
    });


    return (
      <>
        {/* <div 
          style={{ marginRight:"20px", float:"right" }}
        >
          <Button
            type="primary"
            onClick={this.handleModalAdd.bind(this)}
          >
            <IntlMessages id="button.addDepreciation" />
          </Button>
        </div> */}
          

        {this.props.fixedAsset.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : ( 

        <CardWrapper>
          {/* <Col span={2} /> */}
          <Col span={24} style={{ marginTop: "20px",padding: "0 20px 0 20px" }}>
            <div className="isoInvoiceTable">
              <Scrollbars style={{ width: "100%" }}>
                <TableWrapper
                  // size="small"
                  dataSource={dataSource}
                  columns={columns}
                  loading={loading}
                  className="invoiceListTable"
                  bordered={true}
                  pagination={true}
                />
              </Scrollbars>
            </div>
          </Col>
        </CardWrapper>

      )}
    </>
    );
  }
}

const WrappedIndexDepreciation = Form.create({ name: "update_fixed_asset_depreciation" })(IndexDepreciation);

//export default WrappedIndexDepreciation;

const mapStateToProps = state => {
  return { 
    fixedAsset: state.fixedAsset,
    auth : state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedIndexDepreciation);
