import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment";

import ModalAddMaintenance from "./modalAddMaintenance";
import ModalUpdateMaintenance from "./modalUpdateMaintenance";

// import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Col, Spin, Tooltip } from "antd";
import Popconfirms from "../../../../components/feedback/popconfirm";
import actions from "../../../../redux/asset/fixedAsset/actions";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
// import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
// import { stat } from "fs";

// const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class IndexMaintenance extends Component {
  state = {
    visibleAdd: false,
    visibleUpdate: false,
    maintenance_data: null
  };

  handleModalAdd = () => {
    this.setState({
      visibleAdd: !this.state.visibleAdd
    });
  };

  handleModalUpdate = row => {
    this.state.visibleUpdate
      ? this.setState({ visibleUpdate: false, maintenance_data: {} })
      : this.setState({ visibleUpdate: true, maintenance_data: row });
  };

  handleDelete = id => {
    let values = {};
    values.id_fixed_asset_maintenance = id;
    values.is_active = 0;
    this.props.handleSubmitUpdateMaintenance(values);
  };

  listAction = {
    title: "",
    dataIndex: "id_fixed_asset_maintenance",
    rowKey: "id_fixed_asset_maintenance",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Button
            size="small"
            className="invoiceEdtBtn"
            onClick={() => this.handleModalUpdate(row)}
          >
            <i className="ion-android-create" />
          </Button>
        </Tooltip>
        <Popconfirms
          title={<IntlMessages id="maintenance.popconfirm.delete" />}
          onConfirm={this.handleDelete.bind(this, text)}
          okText={<IntlMessages id="yes" />}
          cancelText={<IntlMessages id="no" />}
        >
          <Tooltip title={<IntlMessages id="delete" />}>
            <Button
              className="invoiceDltBtn"
              size="small"
              //style={{ marginRight:"10px" }}
            >
              <i className="ion-trash-b" />
            </Button>
          </Tooltip>
        </Popconfirms>
      </React.Fragment>
    )
  };

  render() {
    // console.log(this.props.fixedAsset.loading);
    const { fixedAsset } = this.props;
    const fixed_asset_maintenances = fixedAsset.fixed_asset
      ? fixedAsset.fixed_asset.fixed_asset_maintenances
      : [];
    const { loading } = fixedAsset;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(data);

    let columns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "service_date",
        rowKey: "service_date",
        width: "10%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },
      {
        title: getLanguage({ id: "sidebar.assetServiceType" }),
        dataIndex: "asset_service_type",
        rowKey: "asset_service_type",
        width: "30%",
        render: (text, row) => {
          const { asset_service_type } = row;
          return (
            <span>
              {asset_service_type.description +
                " " +
                parseFloat(asset_service_type.interval_value) +
                " " +
                getLanguage({ id: asset_service_type.interval_unit })}
            </span>
          );
        }
      },
      {
        title: getLanguage({ id: "serviceParameter" }),
        dataIndex: "service_parameter",
        rowKey: "service_parameter",
        width: "25%",
        render: text => {
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 75) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "note" }),
        dataIndex: "service_note",
        rowKey: "service_note",
        width: "35%",
        render: text => {
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "5%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      }
    ];

    if (permissions.includes("Update Fixed Asset") && columns.length === 5) {
      columns.push(this.listAction);
    }

    Object.keys(fixed_asset_maintenances).map(
      (fixed_asset_maintenance, index) => {
        return dataSource.push({
          ...fixed_asset_maintenances[fixed_asset_maintenance],
          key: fixed_asset_maintenance
        });
      }
    );

    return (
      <>
        <ModalAddMaintenance
          visible={this.state.visibleAdd}
          handleModal={this.handleModalAdd}
          submit={this.props.handleSubmitAddMaintenance}
          fixedAsset={this.props.fixedAsset}
          loading={loading}
        />
        <ModalUpdateMaintenance
          visible={this.state.visibleUpdate}
          maintenance_data={this.state.maintenance_data}
          handleModal={this.handleModalUpdate}
          fixedAsset={this.props.fixedAsset}
          submit={this.props.handleSubmitUpdateMaintenance}
          loading={loading}
        />

        <div style={{ marginRight: "20px", float: "right" }}>
          <Button type="primary" onClick={this.handleModalAdd.bind(this)}>
            <IntlMessages id="button.addMaintenance" />
          </Button>
        </div>

        {this.props.fixedAsset.loading ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (
          <CardWrapper>
            <Col
              span={24}
              style={{ marginTop: "20px", padding: "0 20px 0 20px" }}
            >
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    // size="small"
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    pagination={true}
                  />
                </Scrollbars>
              </div>
            </Col>
          </CardWrapper>
        )}
      </>
    );
  }
}

const WrappedIndexMaintenance = Form.create({
  name: "update_fixed_asset_maintenance"
})(IndexMaintenance);

//export default WrappedIndexMaintenance;

const mapStateToProps = state => {
  return {
    fixedAsset: state.fixedAsset,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  WrappedIndexMaintenance
);
