import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import moment from "moment"

// import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Col, Spin, Tooltip } from "antd";
//import Popconfirms from "../../../../components/feedback/popconfirm";
import actions from "../../../../redux/asset/fixedAsset/actions";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { StatusTag } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
// import { stat } from "fs";


// const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

const redirectFAT = "/dashboard/fixed-asset-transfers"

class IndexTransfer extends Component {

  

  render() {
    // console.log(this.props.fixedAsset.loading);
    const { fixedAsset } = this.props;
    const fixed_asset_transfers = fixedAsset.fixed_asset ? fixedAsset.fixed_asset.fixed_asset_transfers : [];
    const { loading } = fixedAsset;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(data);

    let columns = [
      {
        title: getLanguage({ id: "date"}),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "20%",
        render: text => <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      },
      {
        title: getLanguage({ id: "fromLocation"}),
        dataIndex: "from_location_name",
        rowKey: "from_location_name",
        width: "25%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "toLocation"}),
        dataIndex: "to_location_name",
        rowKey: "to_location_name",
        width: "25%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "qtyTransferred"}),
        dataIndex: "qty",
        rowKey: "qty",
        width: "20%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: "",
        dataIndex: "id_fixed_asset_transfer",
        rowKey: "id_fixed_asset_transfer",
        width: "10%",
        align: "center",
        render: (text) => (
          <>
            <Tooltip
              placement="topLeft"
              title={<IntlMessages id="tooltip.detail" />}
            >
              <Link to={`${redirectFAT}/update/${text}`}>
                <Button 
                  size="small" 
                  className="invoiceEdtBtn"
                >
                  <i className="ion-android-open" />
                </Button>
              </Link>      
            </Tooltip>
          </>
        )
      }
      
    ];

    Object.keys(fixed_asset_transfers).map((fixed_asset_transfer, index) => {
      return dataSource.push({
        ...fixed_asset_transfers[fixed_asset_transfer],
        key: fixed_asset_transfer
      });
    });


    return (
      <>
        {/* <div 
          style={{ marginRight:"20px", float:"right" }}
        >
          <Button
            type="primary"
            onClick={this.handleModalAdd.bind(this)}
          >
            <IntlMessages id="button.addTransfer" />
          </Button>
        </div> */}
          

        {this.props.fixedAsset.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : ( 

        <CardWrapper>
          {/* <Col span={1} /> */}
          <Col span={24} style={{ marginTop: "20px",padding: "0 20px 0 20px" }}>
            <div className="isoInvoiceTable">
              <Scrollbars style={{ width: "100%" }}>
                <TableWrapper
                  // size="small"
                  dataSource={dataSource}
                  columns={columns}
                  loading={loading}
                  className="invoiceListTable"
                  bordered={true}
                  pagination={true}
                />
              </Scrollbars>
            </div>
          </Col>
        </CardWrapper>

      )}
    </>
    );
  }
}

const WrappedIndexTransfer = Form.create({ name: "update_fixed_asset_transfer" })(IndexTransfer);

//export default WrappedIndexTransfer;

const mapStateToProps = state => {
  return { 
    fixedAsset: state.fixedAsset,
    auth : state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedIndexTransfer);
