import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  // state = {
  //   total_cost : 0
  // }

  // componentDidUpdate = (prevProps) => {
  //   if(prevProps.data !== this.props.data && this.props.data){
  //     const data = this.props.data
  //     let total_cost=0;
      
  //     data.fixed_asset_expenditures.map((x)  => {
  //       total_cost += parseFloat(x.base_qty) * parseFloat(x.cost_per_unit)
  //     })

  //     this.setState({
  //       total_cost
  //     })
  //   }  
  // }


  render() {
    const data = this.props.data ? this.props.data : [];

    let fixed_asset_expenditures = [];

    if(data.fixed_asset_expenditures && data.fixed_asset_expenditures.length > 0){
      fixed_asset_expenditures = [...data.fixed_asset_expenditures]

      let asset_value = 0
      fixed_asset_expenditures.map((x)  => {
        asset_value += parseFloat(x.amount)
      })
      // console.log(data)
      fixed_asset_expenditures.push({
        id_fixed_asset_expenditure: 1,
        index_no: "Total",
        account_name: "Total",
        amount: asset_value
      })
    }

    let column = [
      {
        title: getLanguage({id: "no."}),
        dataIndex: "index_no",
        align: "center",
        render: text => {
          if (isNaN(text)) {
            return {
              props: {
                colSpan: 2
              }
            };
          }
          else{
            return text
          }
        }
      },
      {
        title: getLanguage({id: "accountCode"}),
        dataIndex: "account_code", 
        render: (text,row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          else{
            return text
          }
        }
      },
      {
        title: getLanguage({id: "accountName"}),
        dataIndex: "account_name",
        render: (text, row) => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: `${getLanguage({id: "amount"})} (IDR)`,
        dataIndex: "amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
    ];

    const { loading } = this.props;
    const leftSide = [
      {
        label: <IntlMessages id="assetCategories.pageHeader" />,
        description: data.asset_category ? data.asset_category.asset_category_name : null 
      },
      {
        label: <IntlMessages id="assetTypes.pageHeader" />,
        description: data.asset_type ? data.asset_type.asset_type_name : null 
      },
      {
        label: <IntlMessages id="locations.pageHeader" />,
        description: data.location ? data.location.location_name : null 
      },
      {
        label: <IntlMessages id="departments.pageHeader" />,
        description: data.department ? data.department.department_name : null 
      },
      {
        label: <IntlMessages id="fixedAssetCode" />,
        description: <strong>{data.fixed_asset_code} </strong>
      },
      {
        label: <IntlMessages id="fixedAssetName" />,
        description: <strong>{data.fixed_asset_name} </strong>
      },
      {
        label: <IntlMessages id="fixedAssets.add.serialNo" />,
        description: data.serial_no
      },
      {
        label: <IntlMessages id="acquisitionDate" />,
        description:
          data.acquisition_date
            ? moment(
                data.acquisition_date,
                "YYYY-MM-DD HH:mm:ss"
              ).format("DD MMMM YYYY HH:mm:ss")
            : null
        
      },
      {
        label: <IntlMessages id="usageDate" />,
        description:
          data.usage_date
            ? moment(
                data.usage_date,
                "YYYY-MM-DD HH:mm:ss"
              ).format("DD MMMM YYYY")
            : null
        
      },
      {
        label: <IntlMessages id="depreciationEndDate" />,
        description:
          data.depreciation_end_date
            ? moment(
                data.depreciation_end_date,
                "YYYY-MM-DD HH:mm:ss"
              ).format("DD MMMM YYYY")
            : null
        
      },
      
    ];

    const rightSide = [
      {
        label: <IntlMessages id="assetTypes.add.depreciationMethod" />,
        description: data.depreciation_method === 1
          ? <IntlMessages id="Non Depreciable" />
          : data.depreciation_method === 3
            ? <IntlMessages id="Double Declining" />
            : <IntlMessages id="Straight Line" />
            
      },
      {
        label: <IntlMessages id="estimatedlife" />,
        description: data.estimated_life_year + " Year " + data.estimated_life_month + " Month"
      }, 
      {
        label: <IntlMessages id="depreciationRate" />,
        description: <><NumberFormat value={data.depreciation_rate} />{"%"}</>
      }, 
      {
        label: <IntlMessages id="intangible" />,
        description: data.intangible === 1
          ? <IntlMessages id="yes" />
          : <IntlMessages id="no" />
      }, 
      {
        label: <IntlMessages id="assetTypes.add.assetAccount" />,
        description: data.account_code_asset + " (" + data.account_name_asset + ")"
      }, 
      {
        label: <IntlMessages id="assetTypes.add.depreciationAccount" />,
        description: data.account_code_depreciation + " (" + data.account_name_depreciation + ")"
      }, 
      {
        label: <IntlMessages id="assetTypes.add.expenseAccount" />,
        description: data.account_code_expense + " (" + data.account_name_expense + ")"
      }, 
      {
        label: <IntlMessages id="quantity" />,
        description: <NumberFormat value={data.qty} />
      }, 
      // {
      //   label: <IntlMessages id="salvageValue" />,
      //   description:  <NumberFormat value={data.salvage_value} />
      //               .toFixed(2)
      //               .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      // }, 
      {
        label: <IntlMessages id="monthlyDepreciation" />,
        description: <NumberFormat value={data.monthly_depreciation} />
      }, 
      {
        label: <IntlMessages id="valuePerQuantity" />,
        description: data.qty > 0 
          ? <NumberFormat value={parseFloat(data.book_value/data.qty)} />
          : 0
      }, 
      {
        label: <IntlMessages id="assetValue" />,
        description: <NumberFormat value={data.asset_value} />
      }, 
      {
        label: <IntlMessages id="accumulationOfDepreciation" />,
        description: <NumberFormat value={data.depreciation_value} />
      }, 
      {
        label: <IntlMessages id="bookValue" />,
        description:<NumberFormat value={data.book_value} />
      }, 

    ];

    // const addition = [
    // ];

    // const total = [
    //   {
    //     label: <IntlMessages id="totalCost" />,
    //     amount: this.state.total_cost,
    //     parseAmount: true
    //   },
    //   {
    //     label: <IntlMessages id="totalExpense" />,
    //     amount: data.total_expense,
    //     parseAmount: true
    //   },
    //   {
    //     label: <IntlMessages id="totalAmount" />,
    //     amount: data.total_amount,
    //     parseAmount: true
    //   },
    // ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.fixedAsset" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.transaction_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                            data.transaction_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="expenditureDetails"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={fixed_asset_expenditures}
                rowKey={row => row.id_fixed_asset_expenditure}
                pagination={false}
              />
            </Col>
          </Row>

          {/* <Row type="flex" style={{ marginTop: "24px" }}>
            <Col span={12}>
              <TotalField data={addition} />
            </Col>
            <Col span={12}>
              <TotalField data={total} />
            </Col>
          </Row> */}

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() => document.title = getFileName("fixed_asset", null, false)}
          onAfterPrint={() => document.title = siteConfig.documentTitle}
        />
      </>
    );
  }
}

export default Print;
