import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import FixedAssetUpdateForm from "./updateForm";
import FixedAssetDocUpdateForm from "./updateDocForm";
import FixedAssetNoteUpdateForm from "./indexNote";
import FixedAssetMaintenanceUpdateForm from "./indexMaintenance";
import FixedAssetDepreciation from "./indexDepreciation";
import FixedAssetRevaluation from "./indexRevaluation";
import FixedAssetTransfer from "./indexTransfer";
import History from "./history";


import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/fixedAsset/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";
import Print from "./print"

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/fixed-assets";


class UpdateFixedAsset extends Component {

  state = {
    visible: false,
  }
  // Get All data diawal
  componentDidMount() {
    const { editFixedAsset } = this.props;
    const { id } = this.props.match.params;
    editFixedAsset(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.fixedAsset.error) {
      notification("error", this.props.fixedAsset.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.fixedAsset.success) {
      notification("success", this.props.fixedAsset.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updateFixedAsset } = this.props;
    const { id } = this.props.match.params;

    updateFixedAsset(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelFixedAsset } = this.props;
    const { id } = this.props.match.params;

    cancelFixedAsset(id, value);
  }

  handleSubmitAddNote = value => {
    const { storeFixedAssetNote } = this.props;
    const { id } = this.props.match.params;
    storeFixedAssetNote({...value, id_fixed_asset: id})
  }

  handleSubmitUpdateNote = value => {
    const { updateFixedAssetNote } = this.props;
    updateFixedAssetNote(value.id_fixed_asset_note, value);
  }

  handleSubmitAddMaintenance = value => {
    const { storeFixedAssetMaintenance } = this.props;
    const { id } = this.props.match.params;
    storeFixedAssetMaintenance({...value, id_fixed_asset: id})
  }

  handleSubmitUpdateMaintenance = value => {
    const { updateFixedAssetMaintenance } = this.props;
    updateFixedAssetMaintenance(value.id_fixed_asset_maintenance, value);
  }

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };

  render() {
    // console.log(this.props);
    const { fixed_asset, loading } = this.props.fixedAsset
    const data = fixed_asset ? fixed_asset : []
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>  
        <Box>
          <PageHeader title={<IntlMessages id="fixedAssets.update.title" />}
            style={{ margin:0 }}
          >
             <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={fixed_asset} />

            <Button 
              style={{ marginLeft: "5px" }} 
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={fixed_asset ? fixed_asset.description : null}
          />
          
          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="fixedAssets.tab.detail" />} 
              key="1" 
            >
              {this.props.fixedAsset.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <FixedAssetUpdateForm
                  submit={this.handleSubmit}
                  fixedAsset={this.props.fixedAsset}
                  permissions={permissions}
                  processFixedAsset={this.props.processFixedAsset}
                  handleSubmitCancel={this.handleSubmitCancel}
                  getItems={this.props.getItems}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="fixedAssets.tab.notes" />} 
              key="2"
            >  
              {this.props.fixedAsset.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <FixedAssetNoteUpdateForm
                  handleSubmitAddNote={this.handleSubmitAddNote}
                  handleSubmitUpdateNote={this.handleSubmitUpdateNote}
                  fixedAsset={this.props.fixedAsset}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="fixedAssets.tab.pictures" />} 
              key="3"
            >  
             {this.props.fixedAsset.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <FixedAssetDocUpdateForm
                  submit={this.handleSubmit}
                  fixedAsset={this.props.fixedAsset}
                  downloadDocument={this.props.downloadDocument}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="fixedAssets.tab.maintenances" />} 
              key="4"
              disabled={
                data.status === 1 
                  ? true
                  : false
              }
            >  
              {this.props.fixedAsset.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <FixedAssetMaintenanceUpdateForm
                  handleSubmitAddMaintenance={this.handleSubmitAddMaintenance}
                  handleSubmitUpdateMaintenance={this.handleSubmitUpdateMaintenance}
                  submit={this.handleSubmit}
                  fixedAsset={this.props.fixedAsset}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="fixedAssets.tab.depreciation" />} 
              key="5"
              disabled={
                data.status === 1 
                  ? true
                  : false
              }
            >  
              {this.props.fixedAsset.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <FixedAssetDepreciation
                  fixedAsset={this.props.fixedAsset}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="fixedAssets.tab.revaluation" />} 
              key="6"
              disabled={
                data.status === 1 
                  ? true
                  : false
              }
            >  
              {this.props.fixedAsset.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <FixedAssetRevaluation
                  fixedAsset={this.props.fixedAsset}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="fixedAssets.tab.transfer" />} 
              key="7"
              disabled={
                data.status === 1 
                  ? true
                  : false
              }
            >  
              {this.props.fixedAsset.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <FixedAssetTransfer
                  fixedAsset={this.props.fixedAsset}
                />    
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    fixedAsset: state.fixedAsset, 
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateFixedAsset);
