import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment"

import ModalAddNote from "./modalAddNote"
import ModalUpdateNote from "./modalUpdateNote"

// import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Col, Spin, Tooltip, } from "antd";
import Popconfirms from "../../../../components/feedback/popconfirm";
import actions from "../../../../redux/asset/fixedAsset/actions";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";
// import { stat } from "fs";


// const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class IndexNote extends Component {
  state = {
    visibleAdd : false,
    visibleUpdate: false,
    note_data: null,
  }


  handleModalAdd = () => {
    this.setState({
      visibleAdd : !this.state.visibleAdd
    })
  }

  handleModalUpdate = row => {
    this.state.visibleUpdate 
      ? this.setState({ visibleUpdate: false, note_data: {} })
      : this.setState({ visibleUpdate: true, note_data: row });
  }

  handleDelete = (id) => {
    let values = {};
    values.id_fixed_asset_note = id;
    values.is_active = 0;
    this.props.handleSubmitUpdateNote(values)
  }


  listAction = {
    title: "",
    dataIndex: "id_fixed_asset_note",
    rowKey: "id_fixed_asset_note",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Button 
            size="small" 
            className="invoiceEdtBtn"
            onClick={() => this.handleModalUpdate(row)}
          >
            <i className="ion-android-create" />
          </Button>
        </Tooltip>
        <Popconfirms
            title={<IntlMessages id="note.popconfirm.delete" />}
            onConfirm={this.handleDelete.bind(this, text)}
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          >
            <Tooltip
              title={<IntlMessages id="delete" />}
            >
              <Button
                className="invoiceDltBtn"
                size="small"
                //style={{ marginRight:"10px" }}
                
              >
                <i className="ion-trash-b" />
              </Button>
            </Tooltip>
          </Popconfirms>
      </React.Fragment>
    )
  };

  render() {
    // console.log(this.props.fixedAsset.loading);
    const { fixedAsset } = this.props;
    const fixed_asset_notes = fixedAsset.fixed_asset ? fixedAsset.fixed_asset.fixed_asset_notes : [];
    const { loading } = fixedAsset;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(data);

    let columns = [
      {
        title: getLanguage({ id: "date"}),
        dataIndex: "note_date",
        rowKey: "note_date",
        width: "15%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },
      {
        title: getLanguage({ id: "note"}),
        dataIndex: "note",
        rowKey: "note",
        width: "60%",
        render: text => {
          if (text && text.length > 75) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 75) + "..."}
              </Tooltip>
            );
          }
          else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy"}),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "25%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
    ];

    if (
      permissions.includes("Update Fixed Asset") &&
      columns.length === 3
    ) {
      columns.push(this.listAction);
    }

    Object.keys(fixed_asset_notes).map((fixed_asset_note, index) => {
      return dataSource.push({
        ...fixed_asset_notes[fixed_asset_note],
        key: fixed_asset_note
      });
    });


    return (
      <>
        <ModalAddNote
          visible={this.state.visibleAdd}
          handleModal={this.handleModalAdd}
          submit={this.props.handleSubmitAddNote}
          fixedAsset={this.props.fixedAsset}
          loading={loading}
        />
        <ModalUpdateNote
          visible={this.state.visibleUpdate}
          note_data={this.state.note_data}
          handleModal={this.handleModalUpdate}
          submit={this.props.handleSubmitUpdateNote}
          loading={loading}
        />
        
        <div 
          style={{ marginRight:"20px", float:"right" }}
        >
          <Button
            type="primary"
            onClick={this.handleModalAdd.bind(this)}
          >
            <IntlMessages id="button.addNote" />
          </Button>
        </div>
          

        {this.props.fixedAsset.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : ( 

        <CardWrapper>
          <Col span={24} style={{ marginTop: "20px",padding: "0 20px 0 20px" }}>
            <div className="isoInvoiceTable">
              <Scrollbars style={{ width: "100%" }}>
                <TableWrapper
                  // size="small"
                  dataSource={dataSource}
                  columns={columns}
                  loading={loading}
                  className="invoiceListTable"
                  bordered={true}
                  pagination={true}
                />
              </Scrollbars>
            </div>
          </Col>
        </CardWrapper>

      )}
    </>
    );
  }
}

const WrappedIndexNote = Form.create({ name: "update_fixed_asset_note" })(IndexNote);

//export default WrappedIndexNote;

const mapStateToProps = state => {
  return { 
    fixedAsset: state.fixedAsset,
    auth : state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedIndexNote);
