import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Col, Spin, Upload, Tooltip, Icon } from "antd";
import Popconfirms from "../../../../components/feedback/popconfirm";
import actions from "../../../../redux/asset/fixedAsset/actions";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

// import { stat } from "fs";

// const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class FixedAssetDocUpdateForm extends Component {
  state = {
    fileList: [],
    //file : null,
    uploading: false
  };

  handleSubmit = e => {
    e.preventDefault();
    //console.log("");

    this.props.form.validateFieldsAndScroll((err, value) => {
      // console.log(err)
      if (!err) {
        this.handleUpload();
      }
    });
  };

  componentDidUpdate() {
    // Display error notif
    // if (this.props.customer.error) {
    //   notification("error", this.props.customer.error);
    //   this.props.clearError();
    // }

    if (this.props.fixedAsset.success) {
      this.setState({
        uploading: false
      });
    }
  }

  handleUpload = () => {
    const { fileList } = this.state;
    const { uploadPicture } = this.props;
    let body;
    fileList.forEach(file => {
      // console.log(file);
      body = {
        picture: file,
        description:
          this.props.form.getFieldValue("description") === undefined
            ? null
            : this.props.form.getFieldValue("description")
      };
    });

    // console.log(body)
    this.setState({
      uploading: true
    });

    uploadPicture(this.props.fixedAsset.fixed_asset.id_fixed_asset, body);
  };

  handleDelete = id => {
    const { deletePicture } = this.props;
    deletePicture(id);
  };

  listAction = {
    title: "",
    dataIndex: "id_fixed_asset_picture",
    rowKey: "id_fixed_asset_picture",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <React.Fragment>
        <div>
          <Tooltip title={<IntlMessages id="download" />}>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href={
                localStorage.getItem("server") + row.document_path
              }
              download
            >
              <Button className="invoiceRstBtn" size="small">
                <i className="ion-android-download" />
              </Button>
            </a>
          </Tooltip>

          <Popconfirms
            title={<IntlMessages id="picture.popconfirm.delete" />}
            onConfirm={this.handleDelete.bind(this, text)}
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          >
            <Tooltip title={<IntlMessages id="delete" />}>
              <Button className="invoiceDltBtn" size="small">
                <i className="ion-trash-b" />
              </Button>
            </Tooltip>
          </Popconfirms>
        </div>
      </React.Fragment>
    )
  };

  render() {
    // console.log(this.props.fixedAsset.loading);
    const { getFieldDecorator } = this.props.form;
    const { fixedAsset, downloadPicture } = this.props;
    const pictures = fixedAsset.fixed_asset
      ? fixedAsset.fixed_asset.pictures
      : [];
    const { loading } = fixedAsset;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const { fileList } = this.state;
    const props = {
      listType: "picture",
      multiple: false,
      onRemove: file => {
        this.setState(state => {
          const index = state.fileList.indexOf(file);
          const newFileList = state.fileList.slice();
          newFileList.splice(index, 1);
          return {
            fileList: newFileList
          };
        });
      },
      beforeUpload: file => {
        this.setState({
          fileList: [file]
        });
        return false;
      },
      fileList
    };

    let columns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "uploaded_date",
        rowKey: "uploaded_date",
        width: "25%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "pictureDescription" }),
        dataIndex: "document_path",
        rowKey: "document_path",
        width: "50%",
        render: (text, row) => {
          return (
            // <a href={localStorage.getItem('server') + text} download>
            row.description ? row.description : "-"
            // </a>
          );
        }
      },
      {
        title: getLanguage({ id: "uploadedBy" }),
        dataIndex: "uploaded_by",
        rowKey: "uploaded_by",
        width: "25%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      }
    ];

    if (
      permissions.includes("Upload Doc Fixed Asset") &&
      columns.length === 3
    ) {
      columns.push(this.listAction);
    }

    Object.keys(pictures).map((picture, index) => {
      return dataSource.push({
        ...pictures[picture],
        key: picture
      });
    });

    return (
      <div>
        {this.props.fixedAsset.loading ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (
          <Form layout="vertical" onSubmit={this.handleSubmit}>
            <div style={{ padding: "0 20px 0 20px" }}>
              {permissions.includes("Upload Doc Fixed Asset") ? (
                <React.Fragment>
                  <Col span={8}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="pictureDescription" />}
                    >
                      {getFieldDecorator("description", {
                        // initialValue: ,
                        rules: [
                          // {
                          //   required: true,
                          //   message: <IntlMessages id="required" />
                          // },
                          {
                            max: 30,
                            message: <IntlMessages id="maxLength30" />
                          }
                        ]
                      })(
                        <Input
                          // type="file"
                          //onChange={this.handleFile.bind(this)}
                          style={{ width: "100%", height: "35px" }}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={4} style={{ marginTop: "20px" }}>
                    <Upload {...props}>
                      <Button>
                        <Icon type="upload" /> <IntlMessages id="selectFile" />
                      </Button>
                    </Upload>
                  </Col>
                  <Button
                    type="primary"
                    onClick={this.handleUpload}
                    disabled={fileList.length === 0}
                    loading={loading}
                    style={{ marginTop: "20px", marginLeft: "0%" }}
                  >
                    {loading ? (
                      <IntlMessages id="uploading" />
                    ) : (
                      <IntlMessages id="upload" />
                    )}
                  </Button>
                </React.Fragment>
              ) : null}

              <Col span={24} style={{ marginTop: "20px" }}>
                <CardWrapper>
                  <div className="isoInvoiceTable">
                    <Scrollbars style={{ width: "100%" }}>
                      <TableWrapper
                        // size="small"
                        dataSource={dataSource}
                        columns={columns}
                        loading={loading}
                        className="invoiceListTable"
                        bordered={true}
                        pagination={true}
                      />
                    </Scrollbars>
                  </div>
                </CardWrapper>
              </Col>
            </div>
          </Form>
        )}
      </div>
    );
  }
}

const WrappedFixedAssetDocUpdateForm = Form.create({
  name: "update_fixed_asset_picture"
})(FixedAssetDocUpdateForm);

//export default WrappedFixedAssetDocUpdateForm;

const mapStateToProps = state => {
  return {
    fixedAsset: state.fixedAsset,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  WrappedFixedAssetDocUpdateForm
);
