import React, { Component } from "react";

import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import {
  Form, Input,
  // Select, 
  Col, Row, Modal, DatePicker
} from "antd";

// const { Option } = Select;
const { TextArea } = Input;

class ModalDetail extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.note_date = values.note_date.format("YYYY-MM-DD")
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const { loading } = this.props;
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="addNote" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="50%"
        okText={<IntlMessages id="button.submit" />}
        cancelText={<IntlMessages id="button.cancel" />}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row type="flex" justify="start" gutter={6}>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={
                  <IntlMessages id="date" />
                }
              >
                {getFieldDecorator("note_date", {
                  initialValue: moment(),
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <DatePicker
                    style={{ width: "100%" }}
                    showTime
                    format="DD MMMM YYYY"
                    // onChange={this.handleChangeTransDate}
                    // onOpenChange={this.handleChangeTransDate}
                    allowClear={false}
                    disabledDate={e => this.props.fixedAsset.fixed_asset
                      ? e.valueOf() < moment(this.props.fixedAsset.fixed_asset.acquisition_date, "YYYY-MM-DD")
                      : e.valueOf() < moment()
                    }
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Form.Item
                {...formTextArea}
                label={<IntlMessages id="note" />}
              >
                {getFieldDecorator("note", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <TextArea
                    // placeholder="Notes"
                    rows={5}
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedDetailModal = Form.create({ name: "modal_add_note" })(
  ModalDetail
);

export default WrappedDetailModal;
